#toggle-nav-label {
  color: #000000;
  font-size: 16px;
  cursor: pointer;
  float: right;
  top: 5px;
  position: relative;
}

.close {
  display: none;
}

.open {
  display: block;
  width: 25px;
  height: 25px;
  background-image: url("../images/open_menu.png");
}

#toggle-nav {
  display: none;
}

#toggle-nav:checked {

  & ~ .open {
    display: none;
  }
  & ~ .close {
    display: block;
    width: 25px;
    height: 25px;
    background-image: url("../images/close_menu.jpg");
  }
  & ~ .nav-box {
    display: block;
    //z-index: 400;
  }

}

#toggle-nav:checked ~ #toggle-nav-label {
  //background: #fff;
  color: rgba(0, 0, 0, 0.8);
}

